.App{
min-height: 100vh;
background: #CC95C0;  /* fallback for old browsers */
background-image: linear-gradient(to right, #ffecd2 0%, #fcb69f 100%);

}
.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}
.search-button{
  background-color: lightcoral;
  border: none;
  padding: 10px 20px;
  color: #fff;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}